/* global window */
import React from "react"
import Layout from "../layout"
import Gallery from '../Gallery'
import { useStaticQuery, graphql } from "gatsby"

const IndexPage = ({ folder }) => {

    const data = useStaticQuery(graphql`
    {
    allFile(filter: {relativeDirectory: { regex: "/products/" }}) {
        edges {
        node {
            publicURL
            name
            relativeDirectory
        }
        }
    }
    }
    `)
    console.log(data.allFile.edges)
  const photos = data.allFile.edges
  .filter(f => {
      return f.node.relativeDirectory.replace('products', '').replace(/\//g, '') === (typeof window !== 'undefined' && window.location.pathname.replace(/\//g, ''))
    })
  .map((file, index) => ({ src: file.node.publicURL, width: 1, height: 1 }))
 return <Layout>
  <Gallery photos={photos}></Gallery>
</Layout>
}

export default IndexPage
